body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  padding: 10px;
}

.lineInput {
  background-color: grey;
  border: none;
  width: 475px;
  padding: 0.2em;
  font-size: 1.2em;
  -webkit-transition: 0.1s;
  transition: 0.1s;
  border-radius: 3px;
}

.lineInput:hover, .lineInput:focus {
  background-color: aliceblue;
  -webkit-transition: 0.1s;
  transition: 0.1s;
}

.itemsContainer {
  width: auto;
  padding: 1em;
  height: 90vh;
  overflow: auto;
}

.poem {
  height: 400px;
  overflow: auto;
  font-size: 2em;
  font-family: serif;
}

button.btn {
  margin: 4px;
}
